.rpqr-button-caption {
   color: #555555;
   font-size: 16px;
   font-weight: bold;
}

.rpqr-button {
  width: 90vw;
  border-radius: 8px;
  background-color: #A373F9;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.rpqr-button-disabled {
   width: 90vw;
   border-radius: 8px;
  background-color: #DECEFD;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
