.rpqr-button-dashboard {
  display: flex;
  flex-direction: column;
}

.rpqr-scanner-container {
   /* width: 90vw; */
   margin: 0 auto;
   /* padding: 10px; */
}

.rpqr-scanner-container div {
   box-shadow: rgba(180, 0, 255, 0.4) 0px 0px 0px 5px inset !important;
   border: 50px solid rgba(20, 0, 25, 0.3) !important;
}

.rpqr-dashboard-header {
   padding: 20px;
}


.rpqr-close-button {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  border-radius: 4px;
  background-color: black;
  border: none;
  color: white;
  padding: 5px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 8px;
}

.rpqr-loading-image {
   margin-top: 50px;
   margin-bottom: 10px;
   height: 50px;
   width: 50px;
   animation: rotation 2s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
